import React, { useState, useEffect, useCallback } from 'react';
import { navigate } from 'gatsby';
import { FaPhone, FaEnvelope, FaBars, FaTimes, FaQuoteRight, FaFacebookF, FaInstagram, FaTwitter, FaLinkedinIn } from 'react-icons/fa';
import './header.css';
import { Owner, Profile } from '../../content/content';

const Header: React.FC = () => {
  const [isTopbarVisible, setIsTopbarVisible] = useState(true);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [isScrolled, setIsScrolled] = useState(false);
  const [activeLink, setActiveLink] = useState('home');

  // Scroll handling with throttle
  useEffect(() => {
    let lastScrollY = window.pageYOffset;
    let ticking = false;

    const updateScrollState = () => {
      const scrollY = window.pageYOffset;
      
      // Hide topbar on scroll down, show on scroll up
      if (scrollY > lastScrollY && scrollY > 100) {
        setIsTopbarVisible(false);
      } else if (scrollY < lastScrollY) {
        setIsTopbarVisible(true);
      }
      
      // Add shadow to navbar when scrolled
      setIsScrolled(scrollY > 10);
      
      lastScrollY = scrollY;
      ticking = false;
    };

    const handleScroll = () => {
      if (!ticking) {
        window.requestAnimationFrame(updateScrollState);
        ticking = true;
      }
    };

    window.addEventListener('scroll', handleScroll, { passive: true });
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // Lock body scroll when mobile menu is open
  useEffect(() => {
    if (isMobileMenuOpen) {
      document.body.style.overflow = 'hidden';
      document.body.style.touchAction = 'none';
    } else {
      document.body.style.overflow = '';
      document.body.style.touchAction = '';
    }

    return () => {
      document.body.style.overflow = '';
      document.body.style.touchAction = '';
    };
  }, [isMobileMenuOpen]);

  // Handle link click
  const handleLinkClick = useCallback((linkId: string) => {
    setActiveLink(linkId);
    setIsMobileMenuOpen(false);
    
    // Navigate to different pages
    if (linkId === 'home') {
      window.location.href = '/';
    } else if (linkId === 'services') {
      // Scroll to services section on home page or navigate to home with hash
      if (window.location.pathname === '/') {
        const element = document.getElementById('services');
        if (element) {
          element.scrollIntoView({ behavior: 'smooth' });
        }
      } else {
        window.location.href = '/#services';
      }
    } else if (linkId === 'projects') {
      // Scroll to projects section on home page or navigate to home with hash
      if (window.location.pathname === '/') {
        const element = document.getElementById('projects');
        if (element) {
          element.scrollIntoView({ behavior: 'smooth' });
        }
      } else {
        window.location.href = '/#projects';
      }
    } else if (linkId === 'about') {
      navigate('/About');
    } else if (linkId === 'contact') {
      navigate('/Contact');
    }
  }, []);

  // Navigation items
  const navItems = [
    { id: 'home', label: 'Home' },
    { id: 'services', label: 'Services' },
    { id: 'projects', label: 'Projects' },
    { id: 'about', label: 'About' },
    { id: 'contact', label: 'Contact' },
  ];

  return (
    <>
      {/* Topbar - Hidden on mobile */}
      <div className={`topbar ${isTopbarVisible ? 'topbar--visible' : 'topbar--hidden'} ${isScrolled ? 'topbar--scrolled' : ''}`}>
        <div className="topbar__container">
          <div className="topbar__content">
            {/* Contact Info */}
            <div className="topbar__contact">
              <div className="topbar__contact-item">
                <FaPhone className="topbar__icon" />
                <a className="topbar__contact-link" href={`tel:${Profile.phone}`}>{Profile.phone}</a>
              </div>
              <div className="topbar__contact-item">
                <FaEnvelope className="topbar__icon" />
                <span className="topbar__text">{Profile.email}</span>
              </div>
            </div>

            {/* Social Media Icons */}
            <div className="topbar__social">
              <a href="https://facebook.com" className="topbar__social-link" aria-label="Facebook">
                <FaFacebookF className="topbar__social-icon" />
              </a>
              <a href="https://instagram.com" className="topbar__social-link" aria-label="Instagram">
                <FaInstagram className="topbar__social-icon" />
              </a>
              <a href="https://twitter.com" className="topbar__social-link" aria-label="Twitter">
                <FaTwitter className="topbar__social-icon" />
              </a>
              <a href="https://linkedin.com" className="topbar__social-link" aria-label="LinkedIn">
                <FaLinkedinIn className="topbar__social-icon" />
              </a>
            </div>
          </div>
        </div>
      </div>

      {/* Main Navbar */}
      <nav className={`navbar ${isScrolled ? 'navbar--scrolled' : ''}`}>
        <div className="navbar__container">
          <div className="navbar__content">
            {/* Logo */}
            <div className="navbar__logo">
              <span className="navbar__logo-text">Bullseye Exteriors</span>
            </div>

            {/* Desktop Navigation */}
            <ul className="navbar__links">
              {navItems.map((item) => (
                <li key={item.id} className="navbar__item">
                  <button
                    className={`navbar__link ${
                      activeLink === item.id ? 'navbar__link--active' : ''
                    }`}
                    onClick={() => handleLinkClick(item.id)}
                  >
                    {item.label}
                  </button>
                </li>
              ))}
            </ul>

            {/* Desktop CTA Button */}
            <button className="navbar__cta"  onClick={()=>navigate('/Contact')}>
              <FaQuoteRight className="navbar__cta-icon" />
              Free Estimate
            </button>

            {/* Mobile Menu Button */}
            <button
              className="navbar__menu-button"
              onClick={() => setIsMobileMenuOpen(true)}
              aria-label="Open menu"
            >
              <FaBars className="navbar__menu-icon" />
            </button>
          </div>
        </div>
      </nav>

      {/* Mobile Menu Overlay */}
      <div className={`mobile-menu ${isMobileMenuOpen ? 'mobile-menu--open' : ''}`}>
        <div className="mobile-menu__overlay" onClick={() => setIsMobileMenuOpen(false)} />
        
        <div className="mobile-menu__content">
          {/* Mobile Menu Header */}
          <div className="mobile-menu__header">
            <div className="mobile-menu__logo">Bullseye Exteriors</div>
            <button
              className="mobile-menu__close"
              onClick={() => setIsMobileMenuOpen(false)}
              aria-label="Close menu"
            >
              <FaTimes className="mobile-menu__close-icon" />
            </button>
          </div>

          {/* Mobile Navigation Links */}
          <ul className="mobile-menu__links">
            {navItems.map((item) => (
              <li key={item.id} className="mobile-menu__item">
                <button
                  className={`mobile-menu__link ${
                    activeLink === item.id ? 'mobile-menu__link--active' : ''
                  }`}
                  onClick={() => handleLinkClick(item.id)}
                >
                  {item.label}
                </button>
              </li>
            ))}
          </ul>

          {/* Mobile CTA Section */}
          <div className="mobile-menu__footer">
            <div className="mobile-menu__contact">
              <div className="mobile-menu__contact-item">
                <FaPhone className="mobile-menu__contact-icon" />
                        <a className="topbar__contact-link" href={`mailto:${Profile.email}`}>{Profile.email}</a>
              </div>
              <div className="mobile-menu__contact-item">
                <FaEnvelope className="mobile-menu__contact-icon" />
                        <a className="topbar__contact-link" href={`mailto:${Profile.email}`}>{Profile.email}</a>
              </div>
            </div>
            <button className="mobile-menu__cta" onClick={()=>navigate('/Contact')}>
              <FaQuoteRight className="mobile-menu__cta-icon" />
              Free Estimate
            </button>
          </div>
        </div>
      </div>
    </>
  );
};

export default Header;